import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Redirect } from 'react-router-dom';
import { Button } from '../../components';
import { useMainContext } from '../../Context/Main';
import { mocaProfile } from '../../MoocasData/Profile';
import { Container, Wrapper } from './styles';
import { isAuthenticated } from '../../Auth';

const Login: React.FC = () => {
  const { setProfile } = useMainContext();
  const refSelect = useRef<HTMLSelectElement>(null);

  const handleLogin = useCallback(() => {
    const id = refSelect.current?.value;
    setProfile(mocaProfile.find(p => p.id === id) || null);
  }, [setProfile]);

  useEffect(() => {
    console.log('entrou');
    setProfile(null);
  }, [setProfile]);

  if (isAuthenticated()) return <Redirect to="/" />;

  return (
    <Container>
      <Wrapper>
        <h1>Login</h1>

        <select ref={refSelect}>
          <option key="1" value="1">
            Joo - Administrador
          </option>
          <option key="2" value="2">
            Maria
          </option>
        </select>
        <Button onClick={handleLogin}>Autenticar</Button>
      </Wrapper>
    </Container>
  );
};

export default Login;
